.btn{
    background-color: transparent;
    border: 1px solid #d1cece;
    border-radius: 8px;
    margin-right: 10px;
    width: 60px;
    height: 30px;
    font-size: 16px;
    box-shadow: 0 2px 0 0 #d1cece;
}

.btn:active{
    background-color: rgb(0, 149, 255);
    color: #fff;
    box-shadow: none;
}